.about {
  background-repeat: no-repeat;
  @media screen and (max-width: 767px) {
    background-image: url(../image/about/milkbg-mobile.png);
    background-position: 100% 90%;
    background-size: 100%, 100%;
    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      background-image: url(../image/about/milkbg-mobile@2x.png);
    }
  }
  @media screen and (min-width: 1200px) {
    background-image: url(../image/about/milkbg-desktop.png);
    background-position: bottom;
    background-size: 100%, 100%;
    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      background-image: url(../image/about/milkbg-desktop@2x.png);
    }
  }
  background-color: #fff5f6;
  padding-top: 125px;
  padding-bottom: 39px;

  &-title {
    margin-bottom: 26px;
    @media screen and (min-width: 768px) {
      margin-bottom: 50px;
    }
    @media screen and (min-width: 1200px) {
      margin-bottom: 55px;
    }

    &__label {
      font-family: var(--secondary-font);
      @include font(400, 18px, 20px);
      text-align: center;
      letter-spacing: 0.04em;
      text-transform: uppercase;
      color: var(--color-pink-darker-pink);
      margin-bottom: 15px;
      @media screen and (min-width: 768px) {
        @include font(400, 26px, 30px);
      }
      @media screen and (min-width: 1200px) {
        @include font(400, 30px, 34px);
      }
    }

    &__headline {
      font-family: var(--secondary-font);
      @include font(400, 34px, 39px);
      text-align: center;
      letter-spacing: 0.06em;
      text-transform: uppercase;
      color: var(--color-pink-accent);

      @media screen and (min-width: 768px) {
        @include font(400, 48px, 55px);
      }
      @media screen and (min-width: 1200px) {
        @include font(400, 58px, 66px);
      }
    }
  }

  &-description {
    @include font(500, 12px, 19px);
    display: flex;
    flex-direction: column;
    align-items: center;

    @media screen and (min-width: 768px) {
      @include font(500, 14px, 25px);
      letter-spacing: 0.04em;
      flex-direction: row;
      align-items: flex-start;
    }

    &__info {
      @media screen and (max-width: 767px) {
        margin: 0 30px;
        flex-direction: column;
      }
      @media screen and (min-width: 768px) {
        margin-top: 50px;
        margin-right: auto;
        margin-left: 17px;
      }
      @media screen and (min-width: 1200px) {
        margin-top: 55px;
        margin-left: 10px;
      }
    }

    &__image {
      margin-bottom: 46px;
      @media screen and (min-width: 768px) {
        background-size: cover;
        margin-bottom: 0;
      }
    }

    &__label {
      margin-bottom: 25px;
      color: var(--color-black);

      @media screen and (min-width: 768px) {
        margin-bottom: 30px;
        width: 340px;
      }
      @media screen and (min-width: 1200px) {
        @include font(500, 15px, 28px);
        width: 455px;
      }
    }

    &__text {
      margin-bottom: 17px;
      color: var(--color-subtext);

      @media screen and (min-width: 768px) {
        margin-bottom: 62px;
        width: 340px;
      }
      @media screen and (min-width: 1200px) {
        @include font(500, 13px, 27px);
        margin-bottom: 40px;
        width: 455px;
      }
    }

    &__btn {
      @include font(700, 14px, 21px);
      vertical-align: sub;
      @media screen and (min-width: 768px) {
        padding: 12px 31px;
        font-size: 16px;
        line-height: 1.3;
      }
      &-icon {
        margin-left: 13px;
        justify-content: center;
        align-items: center;
        @media screen and (min-width: 768px) {
          margin-left: 13px;
        }
      }
    }
  }
}
